<template>
  <div>
    <v-back></v-back>
    <v-search></v-search>
    <hr>
    <!-- 传参1 -->
    <ul>
      <li class="item" is="router-link" :to="'/movieDetail?id='+item.id" v-for="item in movies" :key="item.id">
        <p>名称：{{item.name}}</p>
        <p>价格：{{item.price | priceFilter}}</p>
        <p>时间：{{item.time | timeFilter}}</p>
      </li>
    </ul>
    <hr>
    <!-- 传参2 -->
    <ul>
      <li class="item1" v-for="item in movies" :key="item.id" @click="toMovieDetail(item.id)">
        <p>名称：{{item.name}}</p>
        <p>价格：{{item.price | priceFilter}}</p>
        <p>时间：{{item.time | timeFilter}}</p>
      </li>
    </ul>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        movies: [{
            id: 1,
            name: '我和我的祖国',
            price: 58.8,
            time:1723654622
          },
          {
            id: 2,
            name: '我和我的家乡',
            price: 39.0,
            time:6433658422
          },
          {
            id: 3,
            name: '大话西游',
            price: 22.2,
            time:2546548652
          }
        ]
      }
    },
    methods: {
      toMovieDetail(id){
        this.$router.push("/movieDetail?id="+id)
      }
    },
    watch: {},
    mounted() {}
  }

</script>
<style scoped>
.item{
  width: 80%;
  margin: 15px auto;
  padding: 15px;
  background: pink;
  display: block;
}
.item1{
    width: 80%;
  margin: 15px auto;
  padding: 15px;
  background: orange;
}
</style>



